// .class { @include respon-img-full; }
@mixin respon-img-full() {
    .category-content {
        .item {
            padding: 20px;
            @include flex-wrap( wrap );
            figure {
                margin: 0 0 15px;
                max-width: 100%;
            }
            .info {
                width: 100%;
            }
        }
    }
}
// .class { @include hover-title-top; }
@mixin hover-title-top() {
    position: relative;
    overflow: hidden;
    .title-news-feature {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        @include transition( 0.5s ease 0s );
        @include transform( translateY(100%) );
    }
    &:hover {
        .title-news-feature {
            @include transform( translateY(0) );
        }
    }
}
// .class { @include hover-img-toa-right-left; }
@mixin hover-img-toa-right-left() {
    overflow: hidden;
    position: relative;
    a {
        &::after, &::before {
            background: rgba(0, 0, 0, 0.2) none repeat scroll 0 0;
            content: "";
            height: 100%;
            position: absolute;
            @include transition( all 0.5s ease 0s );
            width: 100%;
        }
        &::after {
            left: -100%;
            top: -100%;
        }
        &::before {
            bottom: -100%;
            right: -100%;
        }
    }
    &:hover {
        a{
            &::after {
                left: 0;
                top: 0;
            }
            &::before {
                right: 0;
                bottom: 0;
            }           
        }
    }
}
// .class { @include hover-img-toa-right; }
@mixin hover-img-toa-right() {
    overflow: hidden;
    position: relative;
    a::after {
        background: rgba(0, 0, 0, 0.3) none repeat scroll 0 0;
        content: "";
        height: 100%;
        left: -100%;
        position: absolute;
        top: -100%;
        width: 100%;
        @include transition( all 0.5s ease 0s );
    }
    &:hover a::after {
        left: 0;
        top: 0;
    }
}
// .class { @include hover-img-toa; }
@mixin hover-img-toa() {
    overflow: hidden;
    img {
        @include transition( all 0.5s ease 0s );
        &:hover {
            @include transform( scale(1.2, 1.2) );
        }
    }
}
// .class { @include hover-img-toa-item; }
@mixin hover-img-toa-item() {
    figure {
        overflow: hidden;
        img {
            @include transition( all 0.5s ease 0s );
        }
    }
    &:hover {
        figure {
            img {
                @include transform( scale(1.2, 1.2) );
            }
        }
    }
}
// .class { @include hover-img-color; }
@mixin hover-img-color() {
    img {
        @include transition( all 0.5s ease 0s );
        &:hover {
            @include filter( brightness(110%) );
        }
    }
}
// .class { @include hover-img-color-item; }
@mixin hover-img-color-item() {
    figure {
        img {
            @include transition( all 0.5s ease 0s );
        }
    }
    &:hover {
        figure {
            img {
                @include filter( brightness(120%) );
            }
        }
    }
}
// .class { @include hover-img-toa-vuong; }
@mixin hover-img-toa-vuong() {
    position: relative;
    &::after {
        background: rgba(0, 0, 0, 0.3) none repeat scroll 0 0;
        content: "";
        height: calc(100% - 30px);
        left: 50%;
        position: absolute;
        top: 50%;
        width: calc(100% - 30px);
        @include transition( all 0.5s ease 0s );
        @include transform( translate(-50%, -50%) scale(0, 0) );
    }
    &:hover::after {
        @include transform( translate(-50%, -50%) scale(1, 1) );
    }
}
// .class { @include hover-img-xoay-vuong; }
@mixin hover-img-xoay-vuong() {
    position: relative;
    overflow: hidden;
    &::after {
        background: rgba(0, 0, 0, 0.3) none repeat scroll 0 0;
        content: "";
        height: calc(100% - 30px);
        left: 50%;
        position: absolute;
        top: 50%;
        width: calc(100% - 30px);
        @include transition( all 0.5s ease 0s );
        @include transform( translate(-50%, -50%) scale(0, 0) rotate(0deg) );
    }
    &:hover::after {
        @include transform( translate(-50%, -50%) scale(1, 1) rotate(-360deg) );
    }
}






